import React, { Component } from 'react';
import{ Grid, Cell } from 'react-mdl'

class Landing extends Component {
    render(){
        return(
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className="landing-grid">
                    <Cell col={12}>
                        <img
                            
                            src="https://Fionalan727.github.io/Portfolio/images/profile.jpg"
                            alt="avatar"
                            className="avatar-img"
                        />
                        <div className="banner-text">
                            <h1>Full Stack Web Developer</h1>

                            <hr/>

                            <p>HTML / CSS | Bootstrap | JavaScript / jQuery | React | Python | NodeJS | Express | SQL | MongoDB </p>

                            <div className="social-link">

                                {/* Linkedin */}
                                <a href="https://www.linkedin.com/in/yufei-lan-92b340186/"  rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-linkedin-square" aria-hidden="true"/>
                                </a>

                                {/* Github */}
                                <a href="https://github.com/Fionalan727"  rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-github-square" aria-hidden="true"/>
                                </a>

                            </div>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
} ;
   
  
export default Landing;


